                            <div class="col-md-12 mb-3">
                                <label>Ano Letivo</label><br>
                                <select class="form-select" v-model="contrato.ano_lectivo_id">
                                    <option 
                                        v-for="ano in anos" 
                                        :key="ano.id"     
                                        :value="ano.id"
                                    >{{ ano.nome }}</option>    
                                </select>
                            </div>
                            <div class="col-md-12 mb-3">
                                <label>Estado</label><br>
                                <select class="form-select" v-model="contrato.estado">
                                    <option value="Ativo">Ativo</option> 
                                    <option value="Finalizado">Finalizado</option> 
