    <!-- Page wrapper end -->
</template>

<script>
import $ from 'jquery';
import '../static';
import Sidebar from '../components/Sidebar.vue';
import Navbar from '../components/Navbar.vue';
import Footer from '../components/Footer.vue';
import { api, language, numberFormat } from '../../../helpers/api';
import DataTable from 'datatables.net-vue3';
import DataTablesCore from 'datatables.net';
import Select from 'datatables.net-select';
import { mapState } from 'vuex';

DataTable.use(DataTablesCore);
DataTable.use(Select);

export default {
  name: "contrato_auxiliar_view",
  components: { Sidebar, Navbar, Footer, DataTable },
  data() {
    return {
      title: "Contratos auxiliares",
      selectedRow: null,
      contratos: [],
      funcionarios: [],
      anos: [],
      columns: [
        { 
            data: null,
            render:(data) => {
                return data.Funcionario.nome
            }
        },
        { data: 'funcao' },
        { 
            data: 'salario_base',
            render:(data) => {
                return numberFormat(data,2,',','.')
            }
        },
        { 
            data: null,
            render:(data) => {
                return data.AnoLectivo.nome
            }
        },
        {
          data:"estado"
        },
      ],
      contrato: {},
      filter_data: {},
      language: language
    }
  },
  computed: {
    ...mapState(['user'])
  },
  mounted() {
    this.getContratos();
    this.getFuncionarios();
    this.getAnos();
  },
  methods: {
    getAnos() {
      api.get(`/anos/`)
        .then(res => {
          this.anos = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getFuncionarios() {
      api.get(`/funcionarios/grupo/Auxiliar`)
        .then(res => {
          this.funcionarios = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    getContratos() {
      api.get(`/contratos-auxiliar/`)
        .then(res => {
          this.contratos = res.data;
        })
        .catch(err => {
          console.log(err);
        });
    },
    filterData() {
      api.get(`/contratos-auxiliar/ano/${this.filter_data.ano_lectivo_id}`)
        .then(res => {
            if(res.data.length > 0){
                this.contratos = res.data;
            }else{
                this.contratos = []
            }
        })
        .catch(err => {
          console.log(err);
        });
    },
    submitForm() {
      if (this.contrato.id > 0) {
        api.put(`/contratos-auxiliar/${this.contrato.id}`, this.contrato)
          .then(res => {
            if (res.data.message) {
              this.$swal.fire({
                title: "Sucesso",
                text: res.data.message,
                icon: "success"
              });
              this.getContratos();
              $(".btn-secondary").click();
            } else {
              this.$swal.fire({
                title: "Erro",
                text: "Não foi possível atualizar",
                icon: "error"
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        api.post("/contratos-auxiliar/", {...this.contrato, user_id:this.$store.state.user.id})
          .then(res => {
            if (res.data.data.id > 0) {
              this.$swal.fire({
                title: "Sucesso",
                text: res.data.message,
                icon: "success"
              });
              this.getContratos();
              $(".btn-secondary").click();
            } else {
              this.$swal.fire({
                title: "Erro: Não foi possível cadastrar",
                icon: "error"
              });
            }
          })
          .catch(err => {
            console.log(err);
          });
      }
    },
    deleteForm() {
      api.delete(`/contratos-auxiliar/${this.contrato.id}`)
        .then(res => {
          if (res.data.message) {
            this.$swal.fire({
              title: "Sucesso",
              text: res.data.message,
              icon: "success"
            });
            this.getContratos();
            $(".btn-secondary").click();
          } else {
            this.$swal.fire({
              title: "Erro: Não foi possível excluir",
              icon: "error"
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    selectRow() {
      const table = this.$refs.table.dt;
      const self = this;  

      table.rows({ selected: true }).every(function () {
        const data = this.data();
        self.contrato = data;
      });
    },
    clearForm() {
      this.contrato = {};
    },
  },
}
</script>

